import React from 'react'
import Box from './Box'
import { responsive } from './ThemeProvider/theme'

const Source = ({ borderColor, children, note, chart, ...props }) => {
  return (
    <Box
      mt="1.875rem"
      px={responsive('5%', '10%')}
      pt="1rem"
      pb="5em"
      color="custom.textSource"
      fontSize={responsive('0.5em', '0.75em')}
      borderTop="1px solid"
      borderColor={borderColor}
      justify="center"
      {...props}
    >
      {!note && '資料來源：'}{children}
    </Box>
  )
}

export default Source
