import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage, StaticImage } from "gatsby-plugin-image"
import { HiOutlineChevronDoubleDown } from "react-icons/hi";
import styled from '@emotion/styled'
import { keyframes } from '@emotion/react';
import { Icon } from '@chakra-ui/react';
import { BgImage } from "gbimage-bridge"

import Box from '../../../components/Box'
import { Media, responsive } from '../../../components/ThemeProvider/theme'

const GatsbyBgImage = styled(BgImage)`
height: 100%;
width: 100%;
`

const anime = keyframes`
  0%,,
  100% {
    transform: translateY(15%);
  }
  50% {
    transform: translateY(-15%);
  }
`

const DownAnimation = styled(Box)`
  animation: ${anime} 1s infinite;
`

const Banner = ({ onLoad = () => {} }) => {
  const { mobile, tablet, desktop } = useStaticQuery(
    graphql`
      query {
        mobile: file(relativePath: { eq: "containers/HomePage/Banner/mobile.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              width: 640
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
        tablet: file(relativePath: { eq: "containers/HomePage/Banner/tablet.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
        desktop: file(relativePath: { eq: "containers/HomePage/Banner/desk.jpg" }) {
          childImageSharp {
            gatsbyImageData(
              placeholder: BLURRED
              layout: FULL_WIDTH
            )
          }
        }
      }
    `
  )
  const imageMobile = getImage(mobile)
  const imageTablet = getImage(tablet)
  const imageDesk = getImage(desktop)
  return (
    <Box.Relative height="100%">
      <Box as={Media} lessThan="tablet" height="100%">
        <GatsbyBgImage image={imageMobile} onLoad={onLoad} />
        <Box.FullAbs>
          <StaticImage layout="fullWidth" src="mobile_text.png" alt="越永續，越得意" />
        </Box.FullAbs>
      </Box>
      <Box as={Media} at="tablet" height="100%">
        <GatsbyBgImage image={imageTablet} onLoad={onLoad} />
        <Box.FullAbs>
          <StaticImage layout="fullWidth" src="tablet_text.png" alt="越永續，越得意文字" />
        </Box.FullAbs>
      </Box>
      <Box as={Media} greaterThan="tablet" height="100%">
        <GatsbyBgImage image={imageDesk} onLoad={onLoad} />
      </Box>
      <Box.Absolute left="50%" transform="translateX(-50%)" bottom={responsive('1em', '3em')} color="white">
        <DownAnimation>
          <Icon width={responsive('2em', '2.5em')} height={responsive('2em', '2.5em')} as={HiOutlineChevronDoubleDown} />
        </DownAnimation>
      </Box.Absolute>
    </Box.Relative>
  )
}

export default Banner
